<template>
    <div class="kind">
        <div class="Times" v-for="tab in tabs">
            <img :src="tab.img">
            <p>{{tab.text}}</p>
    
        </div>
    
    </div>
</template>
<script>
export default {
    data() {
        return {
            tabs: [{
                text: '豆瓣時間',
                img: "data:image/png;base64,",
                path: '/home'
            }, {
                text: '集市',
                img: "data:image/png;base64,",
                path: '/home'
            }, {
                text: '豆瓣书城',
                img: "data:image/png;base64,",
                path: '/home'
            }, {
                text: '豆瓣视频',
                img: "data:image/png;base64,",
                path: '/home'
            }],
        }
    }
}
</script>

<style scoped>
.kind {
    display: flex;
}

.kind div {
    flex: 1;
    height: 40px;
    padding: 10px 10px;
    text-align: center;
}

.kind div img {
    width: 24px;
    height: 24px;
}

.kind div p {
    font-size: 12px;
    color: #333;
}

.Times {
    flex: 1;
}
</style>




