<template>
    <div class="mit">
        <mt-swipe :auto="3000">
            <mt-swipe-item v-for="(item,index) in list" :key="index">
                <img :src="item.img" width="376" height="200">
            </mt-swipe-item>
    
        </mt-swipe>
    </div>
</template>
<script>
export default {
    data() {
        return {
            list: [
                {
                    "img": "http://img001.mllres.com/images/201708/1502405276363640915.jpg",
                },
                {
                    "img": "http://img004.mllres.com/images/201607/1467327423773766008.jpg",
                },
                {
                    "img": "http://image.meilele.com/images/201609/1474155526796089067.jpg"
                }
            ]
        }
    }

}
</script>
<style scoped>
.mint-swipe {
    overflow: hidden;
    position: relative;
    height: 50%;
}

.mint-swipe-items-wrap {
    position: relative;
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0);
    transform: translateZ(0)
}

.mint-swipe-items-wrap>div {
    float: left;
    position: absolute;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    width: 100%;
    height: 100%;
    display: none
}

.mint-swipe-items-wrap>div.is-active {
    display: block;
    -webkit-transform: none;
    transform: none;
}

.mint-swipe-indicators {
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
}

.mint-swipe-indicator {
    width: 8px;
    height: 8px;
    display: inline-block;
    border-radius: 100%;
    background: #000;
    opacity: 0.2;
    margin: 0 3px;
}

.mint-swipe-indicator.is-active {
    background: #fff;
}
</style>


